link  =  'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx7bfb03bc9c23b615&redirect_uri='+encodeURIComponent (myUrl)+'&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect'
